import { Component, Input, Output, EventEmitter, ViewChild, forwardRef, Renderer } from '@angular/core';
import { BaseComponent } from '../base-component';
import { Policies } from '../security/policies';
import { PolicyInfo } from '../security/policy';

export abstract class BaseControl extends BaseComponent {
    protected isEnabled: boolean = true;
    protected isVisible: boolean = true;
    //
    @Input()
    get enabled(): boolean {
        return this.isEnabled;
    }
    set enabled(value: boolean) {
        if (!this.isPolicyApplied(Policies.enabled)) {
            this.isEnabled = value;
        }
    }

    @Input()
    get visible(): boolean {
        return this.isVisible;
    }
    set visible(value: boolean) {
        if (!this.isPolicyApplied(Policies.visible)) {
            this.isVisible = value;
        }
    }

    //Implementación de políticas
    applyPolicy(policy: PolicyInfo): void {
        if (this.allowPolicies && !this.isPolicyApplied(policy.nombre)) {
            if (policy.nombre.toLowerCase() == Policies.enabled) {
                this.isEnabled = policy.valor.toLowerCase() == 'true' ? true : false;
                this.appliedPolicies.push(policy);
            }
            if (policy.nombre.toLowerCase() == Policies.visible) {
                this.isVisible = policy.valor.toLowerCase() == 'true' ? true : false;
                this.appliedPolicies.push(policy);
            }
        }
    }

}